<template>
  <v-card light class="mr-3 my-5">
    <v-card-title>
      <v-avatar class="mr-2 white--text" color="primary">
        <v-img
          v-if="reply.user"
          alt="Avatar"
          :src="'/uploads/users/' + reply.user.avatar"
        >
          <template v-slot:placeholder>
            <v-row class="fill-height ma-0" align="center" justify="center">
              <v-progress-circular
                indeterminate
                color="primary"
              ></v-progress-circular>
            </v-row>
          </template>
        </v-img>
        <span v-else>
          {{ avatarName }}
        </span>
      </v-avatar>
      <small>{{ reply.user ? reply.user.name : reply.name }}</small>
    </v-card-title>
    <v-card-text>
      {{ reply.text }}
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  props: {
    reply: {
      type: Object,
      required: true
    }
  },
  computed: {
    avatarName() {
      return this.reply.user ? '' : this.reply.name.substr(0, 1)
    }
  }
}
</script>

